import React, { useState, useEffect, useRef } from "react";
import "./todo.css";
import TaskList from "./TaskList";

const Todo = () => {
  const [inputText, setInputText] = useState("");
  const [taskList, setTaskList] = useState([]);
  // Creating a ref for input focus
  const inputRef = useRef(null);

  const handleInput = (e) => {
    setInputText(e.target.value);
  };

  // To Add Item into the list
  const addItem = () => {
    // Validation
    if (inputText !== "") {
      setTaskList([...taskList, inputText]);
      setInputText("");
    } else {
      alert("Please enter the task .");
    }
  };

  // Delete item from the list
  const deleteItem = (id) => {
    setTaskList((oldList) => oldList.filter((newItem, index) => index !== id));
  };

  // Clear all the task in the lists
  const clearList = () => setTaskList([]);

  useEffect(() => {
    // For auto focus
    inputRef.current.focus();
  }, []);

  return (
    <div>
      <div className="todo">
        <h2>
          Todo <span>List</span> App
        </h2>
        <div className="input-field">
          <input
            ref={inputRef}
            value={inputText}
            type="text"
            placeholder="Add Task"
            onChange={handleInput}
            required="required"
          />
          <button onClick={addItem} className="add-btn">
            Add
          </button>
        </div>
        <div className="list-items">
          {taskList.map((task, index) => (
            <TaskList
              text={task}
              id={index}
              key={index}
              onSelect={deleteItem}
            />
          ))}
        </div>
        <button onClick={clearList} className="clear-btn">
          clear Tasks
        </button>
      </div>
    </div>
  );
};

export default Todo;
